"use client";

import Image from "next/image";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { useState } from "react";
// Í∏∞Ï°¥ Î™©Îç∞Ïù¥ÌÑ∞ ÌÉÄÏûÖÏùÑ Í≥ÑÏÜç Ïì∞ÎçîÎùºÎèÑ, DBÏùò Json Ïª¨ÎüºÏùÑ Í∞êÏïàÌï¥ÏÑú Ïú†Ïó∞Ìïú ÌÉÄÏûÖÏúºÎ°ú Î∞õÏûê.
import type { Listing } from "@/lib/types";

// Ïà´Ïûê Ìè¨Îß∑
const fmt = (n?: number | null) =>
  n == null ? "-" : new Intl.NumberFormat("ko-KR").format(n);

// ‚úÖ photosÍ∞Ä string[] | Json(JSON Î¨∏ÏûêÏó¥) | null Îì± Ïó¨Îü¨ ÌòïÌÉúÏùº Ïàò ÏûàÏúºÎãà
//    ÏïàÏ†ÑÌïòÍ≤å Ï≤´ Î≤àÏß∏ ÏÇ¨ÏßÑ URLÎßå ÎΩëÏïÑÏò§Îäî Ìó¨Ìçº
function firstPhoto(photos: unknown): string | undefined {
  if (!photos) return;

  // 1) Ïù¥ÎØ∏ Î∞∞Ïó¥Ïù∏ Í≤ΩÏö∞ (Prisma JsonValueÎèÑ Îü∞ÌÉÄÏûÑÏóêÏÑ† Í∑∏ÎÉ• JS Î∞∞Ïó¥)
  if (Array.isArray(photos) && photos.length && typeof photos[0] === "string") {
    return photos[0] as string;
  }

  // 2) ÌòπÏãú Î¨∏ÏûêÏó¥(JSON)Î°ú Îì§Ïñ¥Ïò® Í≤ΩÏö∞
  if (typeof photos === "string") {
    try {
      const parsed = JSON.parse(photos);
      if (Array.isArray(parsed) && parsed.length && typeof parsed[0] === "string") {
        return parsed[0] as string;
      }
    } catch {
      // JSON ÌååÏã± Ïã§Ìå® Ïãú Î¨¥Ïãú
    }
  }

  // 3) Í∞ùÏ≤¥Ïù∏Îç∞ ÎÇ¥Î∂ÄÏóê Î∞∞Ïó¥Ïù¥ ÏûàÎäî ÌäπÏàò ÏºÄÏù¥Ïä§Îäî Í≥†Î†§ÌïòÏßÄ ÏïäÏùå(ÌïÑÏöî Ïãú ÌôïÏû•)
  return;
}

export default function ListingCard({
  item,
  highlight = false,
  onHover,
}: {
  item: Listing | (Listing & { photos?: unknown }); // DB/Î™©Îç∞Ïù¥ÌÑ∞ ÌòºÏö© ÎåÄÎπÑ
  highlight?: boolean;
  onHover?: (id: number | null) => void;
}) {
  const router = useRouter();
  const [loading, setLoading] = useState<null | "listing" | "loan" | "fitout">(null);

  // üîß Î≥ÄÍ≤Ω Ìè¨Ïù∏Ìä∏: photosÎ•º ÏïàÏ†ÑÌïòÍ≤å Ìï¥ÏÑù
  const thumb = firstPhoto((item as any).photos);

  // ‚úÖ Ïä§Î†àÎìú ÏÉùÏÑ±(ÎòêÎäî Ïû¨ÏÇ¨Ïö©) ÌõÑ /chat/[id]?role=user Î°ú Ïù¥Îèô
  async function startChat(type: "listing" | "loan" | "fitout") {
    try {
      setLoading(type);
      const res = await fetch("/api/chat/threads", {
        method: "POST",
        headers: { "content-type": "application/json" },
        body: JSON.stringify({
          type,               // "listing" | "loan" | "fitout"
          listingId: item.id, // Îß§Î¨º Í∏∞Ï§Ä Ïä§Î†àÎìú Ïû¨ÏÇ¨Ïö© ÌÇ§
          title: item.title,  // Ï†úÎ™©(ÏóÜÏúºÎ©¥ null ÌóàÏö©)
        }),
      });

      const raw = await res.text();
      if (!res.ok) throw new Error(raw || `POST /api/chat/threads ${res.status}`);

      const data = JSON.parse(raw);
      const id: string | undefined = data?.thread?.id;
      if (!id) throw new Error("thread id missing");

      router.push(`/chat/${id}?role=user`);
    } catch (e: any) {
      alert(`Ï±ÑÌåÖÎ∞© Ïó¥Í∏∞ Ïã§Ìå®: ${e?.message || "unknown error"}`);
    } finally {
      setLoading(null);
    }
  }

  return (
    <div
      id={`card-${item.id}`}
      onMouseEnter={() => onHover?.(item.id)}
      onMouseLeave={() => onHover?.(null)}
      className={[
        "rounded-2xl bg-white border shadow p-4 transition",
        highlight ? "border-sky-400 ring-2 ring-sky-300" : "border-neutral-200",
      ].join(" ")}
    >
      <div className="flex items-start justify-between gap-4">
        <div className="flex gap-3">
          {/* Ïç∏ÎÑ§Ïùº */}
          <div className="relative h-16 w-24 rounded-lg overflow-hidden border bg-neutral-100">
            {thumb ? (
              <Image
                src={thumb}
                alt={`${item.title} Ïç∏ÎÑ§Ïùº`}
                fill
                sizes="96px"
                style={{ objectFit: "cover" }}
              />
            ) : (
              <div className="h-full w-full grid place-items-center text-xs text-neutral-500">
                No image
              </div>
            )}
          </div>

          {/* ÌÖçÏä§Ìä∏ */}
          <div>
            <div className="font-semibold">
              {item.title}{" "}
              <span className="text-sm text-neutral-500">
                {(item as any).category ?? ""}
              </span>
            </div>
            <div className="text-sm text-neutral-600">
              {(item as any).address_area ?? ""}
            </div>

            <div className="mt-2 text-sm grid grid-cols-2 gap-x-6 gap-y-1">
              <span>ÏõîÏÑ∏ {fmt((item as any).rent_monthly)}</span>
              <span>ÏûÑÎåÄÎ≥¥Ï¶ùÍ∏à {fmt((item as any).deposit)}</span>
              <span>Í∂åÎ¶¨Í∏à {fmt((item as any).goodwill_price)}</span>
              <span>ÏõîÎß§Ï∂ú {fmt((item as any).monthly_sales)}</span>
              <span>Ï£ºÏ∞® {((item as any).parking_spaces ?? 0) as number}ÎåÄ</span>
            </div>
          </div>
        </div>

        <div className="flex flex-col gap-2 shrink-0">
          <button
            onClick={() => startChat("listing")}
            disabled={loading === "listing"}
            className="rounded-lg bg-sky-500 hover:bg-sky-600 disabled:opacity-60 text-white px-3 py-1.5 text-sm"
          >
            {loading === "listing" ? "Ïó¥Îäî Ï§ë‚Ä¶" : "Îß§Î¨ºÎ¨∏Ïùò"}
          </button>
          <button
            onClick={() => startChat("loan")}
            disabled={loading === "loan"}
            className="rounded-lg bg-sky-400 hover:bg-sky-500 disabled:opacity-60 text-white px-3 py-1.5 text-sm"
          >
            {loading === "loan" ? "Ïó¥Îäî Ï§ë‚Ä¶" : "ÎåÄÏ∂úÎ¨∏Ïùò"}
          </button>
          <button
            onClick={() => startChat("fitout")}
            disabled={loading === "fitout"}
            className="rounded-lg bg-sky-600 hover:bg-sky-700 disabled:opacity-60 text-white px-3 py-1.5 text-sm"
          >
            {loading === "fitout" ? "Ïó¥Îäî Ï§ë‚Ä¶" : "Ïù∏ÌÖåÎ¶¨Ïñ¥Î¨∏Ïùò"}
          </button>

          <Link
            href={`/listing/${item.id}`}
            className="text-center rounded-lg border px-3 py-1.5 text-sm hover:bg-neutral-50"
          >
            ÏÉÅÏÑ∏
          </Link>
        </div>
      </div>
    </div>
  );
}
