// app/listing/[id]/page.tsx
export const dynamic = "force-dynamic";

import ListingMap from "@/components/ListingMap";
import { auth } from "@/lib/auth";
import { prisma } from "@/lib/db";
import type { Listing as MapListing } from "@/lib/mockData";
import { listings as MOCK } from "@/lib/mockData";
import Image from "next/image";
import Link from "next/link";
import { notFound } from "next/navigation";

type VM = {
  id: number;
  title: string;
  category: string;
  address_area: string | null;
  rent_monthly: number | null;
  deposit: number | null;
  goodwill_price: number | null;
  monthly_sales: number | null;
  parking_spaces: number | null;
  lat: number | null;
  lng: number | null;
  ownerId: string | null;
  createdAt: string;
  photos: string[];
};

export default async function ListingDetailPage({
  params,
}: {
  params: Promise<{ id: string }>;
}) {
  const { id } = await params;
  const num = Number(id);
  if (!Number.isFinite(num)) notFound();

  // DB(ÌòÑÏû¨ Ïä§ÌÇ§ÎßàÏóê Ï°¥Ïû¨ÌïòÎäî Ïª¨ÎüºÎßå)
  const db = await prisma.listing.findUnique({
    where: { id: num },
    select: {
      id: true,
      title: true,
      category: true,
      address_area: true,
      rent_monthly: true,
      deposit: true,
      goodwill_price: true,
      monthly_sales: true,
      parking_spaces: true,
      lat: true,
      lng: true,
      ownerId: true,
      createdAt: true,
    },
  });

  // mock(ÏÇ¨ÏßÑ/Ïç∏ÎÑ§Ïùº/ÎØ∏ÎîîÏñ¥/Ï¢åÌëú Î≥¥Í∞ï)
  const mock = (MOCK as any[]).find((x) => x.id === num);

  // ÏÇ¨ÏßÑÏùÄ mockÏùò thumbnail_url + media(photo)Îßå ÏÇ¨Ïö©
  const photosFromMock: string[] = Array.isArray(mock?.media)
    ? mock.media
        .filter((m: any) => m?.file_type === "photo" && m?.file_url)
        .map((m: any) => String(m.file_url))
    : [];
  if (mock?.thumbnail_url) photosFromMock.unshift(String(mock.thumbnail_url));

  const vm: VM | null = db
    ? {
        id: db.id,
        title: db.title ?? "",
        category: db.category ?? (mock?.category ?? ""),
        address_area: db.address_area ?? (mock?.address_area ?? ""),
        rent_monthly: db.rent_monthly ?? null,
        deposit: db.deposit ?? null,
        goodwill_price: db.goodwill_price ?? null,
        monthly_sales: db.monthly_sales ?? null,
        parking_spaces: db.parking_spaces ?? (mock?.parking_spaces ?? null),
        lat: db.lat ?? (mock?.lat ?? null),
        lng: db.lng ?? (mock?.lng ?? null),
        ownerId: db.ownerId ?? null,
        createdAt: db.createdAt.toISOString(),
        photos: photosFromMock,
      }
    : mock
    ? {
        id: Number(mock.id),
        title: String(mock.title ?? ""),
        category: String(mock.category ?? ""),
        address_area: mock.address_area ?? "",
        rent_monthly: mock.rent_monthly ?? null,
        deposit: mock.deposit ?? null,
        goodwill_price: mock.goodwill_price ?? null,
        monthly_sales: mock.monthly_sales ?? null,
        parking_spaces: mock.parking_spaces ?? null,
        lat: mock.lat ?? null,
        lng: mock.lng ?? null,
        ownerId: null,
        createdAt: String(mock.created_at ?? new Date().toISOString()),
        photos: photosFromMock,
      }
    : null;

  if (!vm) notFound();

  // Í∂åÌïú Ï≤¥ÌÅ¨
  const session = await auth();
  const meId = (session?.user as any)?.id as string | undefined;
  const myRole = (session?.user as any)?.role as "ADMIN" | "CLIENT" | undefined;
  const canEdit = !!meId && (vm.ownerId === meId || myRole === "ADMIN");

  // ListingMap.tsx Í∞Ä Í∏∞ÎåÄÌïòÎäî items(Listing[]) ÌòïÌÉúÎ°ú ÎßûÏ∂∞ Ï†ÑÎã¨
  const mapItems: MapListing[] = [
    {
      id: vm.id,
      title: vm.title,
      category: vm.category,
      address_area: vm.address_area ?? "",
      address_full: mock?.address_full ?? (vm.address_area ?? ""),
      lat: vm.lat ?? 37.5665,
      lng: vm.lng ?? 126.978,
      area_pyeong: mock?.area_pyeong,
      rent_monthly: vm.rent_monthly ?? undefined,
      rent_mgmt_fee: mock?.rent_mgmt_fee,
      deposit: vm.deposit ?? undefined,
      goodwill_price: vm.goodwill_price ?? undefined,
      goodwill_negotiable: mock?.goodwill_negotiable,
      takeover_date: mock?.takeover_date,
      description: mock?.description,
      proof_status: mock?.proof_status ?? "unverified",
      status: mock?.status ?? "active",
      created_at: mock?.created_at ?? vm.createdAt,
      financials: mock?.financials,
      media: mock?.media ?? [],
      thumbnail_url: mock?.thumbnail_url,
    } as MapListing,
  ];

  const fmt = (n?: number | null) =>
    n == null ? "-" : new Intl.NumberFormat("ko-KR").format(n);

  return (
    <main className="max-w-5xl mx-auto p-4 space-y-6">
      <header className="flex items-start justify-between gap-3">
        <div>
          <h1 className="text-xl font-semibold">{vm.title}</h1>
          {vm.address_area && (
            <div className="text-sm text-neutral-600">{vm.address_area}</div>
          )}
        </div>
        {canEdit && (
          <Link
            href={`/listing/${vm.id}/edit`}
            prefetch={false}
            className="rounded-lg border px-3 py-1.5 text-sm hover:bg-neutral-50"
          >
            ÏàòÏ†ï
          </Link>
        )}
      </header>

      {/* ÏÇ¨ÏßÑ Í∞§Îü¨Î¶¨ (mock Í∏∞Î∞ò) */}
      {vm.photos.length > 0 && (
        <section className="grid grid-cols-2 md:grid-cols-3 gap-3">
          {vm.photos.slice(0, 6).map((src, i) => (
            <div
              key={`${src}-${i}`}
              className="relative aspect-[4/3] rounded-xl overflow-hidden border bg-neutral-100"
            >
              <Image
                src={src}
                alt={`photo-${i}`}
                fill
                sizes="(max-width: 768px) 50vw, 33vw"
                style={{ objectFit: "cover" }}
              />
            </div>
          ))}
        </section>
      )}

      {/* Ï†ïÎ≥¥ Î∞ïÏä§ */}
      <section className="rounded-2xl border bg-white p-4">
        <div className="grid grid-cols-2 gap-x-8 gap-y-2 text-sm">
          <div>ÏõîÏÑ∏: {fmt(vm.rent_monthly)}</div>
          <div>ÏûÑÎåÄÎ≥¥Ï¶ùÍ∏à: {fmt(vm.deposit)}</div>
          <div>Í∂åÎ¶¨Í∏à: {fmt(vm.goodwill_price)}</div>
          <div>ÏõîÎß§Ï∂ú: {fmt(vm.monthly_sales)}</div>
          <div>Ï£ºÏ∞®: {vm.parking_spaces ?? 0}ÎåÄ</div>
          <div>ÏúÑÏπò: {vm.lat ?? "-"}, {vm.lng ?? "-"}</div>
        </div>
      </section>

      {/* ÎÑ§Ïù¥Î≤Ñ ÏßÄÎèÑ ‚Äî Í∏∞Ï°¥ ListingMap(items Î∞∞Ïó¥) Í∑∏ÎåÄÎ°ú ÏÇ¨Ïö© */}
      {vm.lat != null && vm.lng != null && (
        <section className="rounded-2xl border bg-white p-3">
          <ListingMap
            items={mapItems}
            selectedId={vm.id}
            onSelect={() => {}}
          />
        </section>
      )}
    </main>
  );
}
