"use client";

import { CSSProperties, useEffect, useMemo, useState } from "react";

function clamp(n: number, min: number, max: number) {
  return Math.min(max, Math.max(min, n));
}
const fmt = (n: number) => new Intl.NumberFormat("ko-KR").format(n);

export default function RangeBar({
  label,
  min,
  max,
  step = 100_000,
  valueMin,
  valueMax,
  onChange,
  barHeight = 44,   // ‚úÖ Ìä∏Îûô/ÏÜêÏû°Ïù¥ ÎÜíÏù¥(px). ÏÜêÏû°Ïù¥Í∞Ä ÏÑ∏Î°úÎ°ú ÍΩâ Ï∞®ÎèÑÎ°ù ÎèôÏùº ÏÇ¨Ïö©
  thumbWidth = 12, // ‚úÖ ÏÜêÏû°Ïù¥ Í∞ÄÎ°ú Ìè≠(px) ‚Äî ÏÑ∏Î°úÎ°ú Í∏¥ ÎßâÎåÄ ÎäêÎÇå
  thumbRadius = 6, // ‚úÖ Îë•Í∑º ÏÇ¨Í∞Å Î™®ÏÑúÎ¶¨
}: {
  label: string;
  min: number;
  max: number;
  step?: number;
  valueMin?: number;
  valueMax?: number;
  onChange: (v: { min?: number; max?: number }) => void;
  barHeight?: number;
  thumbWidth?: number;
  thumbRadius?: number;
}) {
  const [lo, setLo] = useState<number>(valueMin ?? min);
  const [hi, setHi] = useState<number>(valueMax ?? max);

  useEffect(() => setLo(valueMin ?? min), [valueMin, min]);
  useEffect(() => setHi(valueMax ?? max), [valueMax, max]);

  const left = useMemo(() => ((lo - min) / (max - min)) * 100, [lo, min, max]);
  const right = useMemo(
    () => 100 - ((hi - min) / (max - min)) * 100,
    [hi, min, max]
  );

  const changeLo = (v: number) => {
    const next = clamp(v, min, hi);
    setLo(next);
    onChange({ min: next });
  };
  const changeHi = (v: number) => {
    const next = clamp(v, lo, max);
    setHi(next);
    onChange({ max: next });
  };

  // CSS Î≥ÄÏàòÎ°ú ÎÜíÏù¥/Ìè≠/Î™®ÏÑúÎ¶¨ Ï†ÑÎã¨ ‚Üí thumbÍ∞Ä Ìä∏Îûô ÎÜíÏù¥Î•º Í∑∏ÎåÄÎ°ú Ï±ÑÏõÄ
  const vars: CSSProperties = {
    // @ts-expect-error custom props
    "--bar-h": `${barHeight}px`,
    // @ts-expect-error custom props
    "--thumb-w": `${thumbWidth}px`,
    // @ts-expect-error custom props
    "--thumb-r": `${thumbRadius}px`,
  };

  return (
    <div className="w-full" style={vars}>
      <div className="flex items-center justify-between text-sm mb-1">
        <span className="text-neutral-700">{label}</span>
        <span className="text-neutral-600">
          {fmt(lo)} ~ {fmt(hi)} Ïõê
        </span>
      </div>

      <div className="relative" style={{ height: "var(--bar-h)" }}>
        {/* Ìä∏Îûô */}
        <div className="absolute inset-0 rounded-full bg-neutral-200" />
        {/* ÏÑ†ÌÉù ÏòÅÏó≠ */}
        <div
          className="absolute inset-y-0 bg-sky-200 rounded-full"
          style={{ left: `${left}%`, right: `${right}%` }}
        />

        {/* ÏµúÏÜå/ÏµúÎåÄ Ïä¨ÎùºÏù¥Îçî (Í≤πÏπ®) */}
        <input
          type="range"
          aria-label={`${label} ÏµúÏÜåÍ∞í`}
          min={min}
          max={max}
          step={step}
          value={lo}
          onChange={(e) => changeLo(Number(e.target.value))}
          className="range"
          style={{ zIndex: 30 }}
        />
        <input
          type="range"
          aria-label={`${label} ÏµúÎåÄÍ∞í`}
          min={min}
          max={max}
          step={step}
          value={hi}
          onChange={(e) => changeHi(Number(e.target.value))}
          className="range"
          style={{ zIndex: 40 }}
        />

        {/* Ïä§ÌÉÄÏùº: Ìä∏ÎûôÏùÄ Ïù¥Î≤§Ìä∏ Ï∞®Îã®, ThumbÎßå ÎìúÎûòÍ∑∏.
           ThumbÎ•º 'ÏÑ∏Î°úÎ°ú Í∏¥ Îë•Í∑º ÏÇ¨Í∞ÅÌòï'ÏúºÎ°ú ÌëúÏãú(Ìä∏Îûô ÎÜíÏù¥=thumb ÎÜíÏù¥). */}
        <style jsx>{`
          .range {
            position: absolute;
            inset: 0;
            width: 100%;
            background: transparent;
            appearance: none;
            pointer-events: none; /* Ìä∏Îûô ÌÅ¥Î¶≠ Î∞©ÏßÄ */
          }
          /* WebKit */
          .range::-webkit-slider-thumb {
            -webkit-appearance: none;
            appearance: none;
            height: var(--bar-h);  /* ‚úÖ Ìä∏Îûô ÎÜíÏù¥ÏôÄ ÎèôÏùº ‚Üí ÏúÑÏïÑÎûò ÍΩâ Ï±ÑÏõÄ */
            width: var(--thumb-w); /* ‚úÖ Ï¢ÅÍ≥† ÏÑ∏Î°úÎ°ú Í∏¥ ÏÜêÏû°Ïù¥ */
            border-radius: var(--thumb-r);
            background: #0ea5e9;    /* sky-500 */
            border: 2px solid #fff; /* ÎåÄÎπÑ */
            box-shadow: 0 1px 2px rgba(0,0,0,.12);
            pointer-events: all;    /* ThumbÎßå Ïù¥Î≤§Ìä∏ ÌóàÏö© */
            cursor: ew-resize;
          }
          .range::-webkit-slider-runnable-track {
            height: var(--bar-h);
            background: transparent;
          }
          .range:focus-visible::-webkit-slider-thumb {
            box-shadow: 0 0 0 3px rgba(14, 165, 233, 0.35);
          }

          /* Firefox */
          .range::-moz-range-thumb {
            height: var(--bar-h);   /* ‚úÖ ÎèôÏùº */
            width: var(--thumb-w);
            border-radius: var(--thumb-r);
            background: #0ea5e9;
            border: 2px solid #fff;
            box-shadow: 0 1px 2px rgba(0,0,0,.12);
            pointer-events: all;
            cursor: ew-resize;
          }
          .range::-moz-range-track {
            height: var(--bar-h);
            background: transparent;
          }
          .range:focus-visible::-moz-range-thumb {
            box-shadow: 0 0 0 3px rgba(14, 165, 233, 0.35);
          }
        `}</style>
      </div>

      <div className="mt-1 text-xs text-neutral-500">
        ÎßâÎåÄÎ•º ÎìúÎûòÍ∑∏ÌïòÏó¨ Î≤îÏúÑÎ•º Ï°∞Ï†àÌïòÏÑ∏Ïöî.
      </div>
    </div>
  );
}
